import React, { useState } from 'react';
import SectionTitle from '../shared/section-titles';
import { useStaticQuery, graphql } from 'gatsby';
import { ChannelRow } from '../shared/popup-channels';
import PopupChannels from '../shared/popup-channels';

const ChannelsSection = () => {
  const data = useStaticQuery(graphql`
  query ChannelsSection {
    polythematicPlaceholder: allFile(filter: {sourceInstanceName: {eq: "channels-polythematic"}}, limit: 12){
      edges {
        node {
          childImageSharp {
            fluid(maxHeight: 40) {
              ...GatsbyImageSharpFluid
            }
          }
        }
      }
    }
    addOnsPlaceholder: allFile(filter: {sourceInstanceName: {eq: "channels-add-ons"}}, limit: 12){
      edges {
        node {
          childImageSharp {
            fluid(maxHeight: 40) {
              ...GatsbyImageSharpFluid
            }
          }
        }
      }
    }
    documentaryPlaceholder: allFile(filter: {sourceInstanceName: {eq: "channels-documentary"}}, limit: 12){
      edges {
        node {
          childImageSharp {
            fluid(maxHeight: 40) {
              ...GatsbyImageSharpFluid
            }
          }
        }
      }
    }
    kidsPlaceholder: allFile(filter: {sourceInstanceName: {eq: "channels-kids"}}, limit: 12){
      edges {
        node {
          childImageSharp {
            fluid(maxHeight: 40) {
              ...GatsbyImageSharpFluid
            }
          }
        }
      }
    }
    moviesPlaceholder: allFile(filter: {sourceInstanceName: {eq: "channels-movies"}}, limit: 12){
      edges {
        node {
          childImageSharp {
            fluid(maxHeight: 40) {
              ...GatsbyImageSharpFluid
            }
          }
        }
      }
    }
    musicPlaceholder: allFile(filter: {sourceInstanceName: {eq: "channels-music"}}, limit: 12){
      edges {
        node {
          childImageSharp {
            fluid(maxHeight: 40) {
              ...GatsbyImageSharpFluid
            }
          }
        }
      }
    }
    sportPlaceholder: allFile(filter: {sourceInstanceName: {eq: "channels-sport"}}, limit: 12){
      edges {
        node {
          childImageSharp {
            fluid(maxHeight: 40) {
              ...GatsbyImageSharpFluid
            }
          }
        }
      }
    }
  }
`)



  const channelInfo = [
    {
      title: '-',
      channelData: data.polythematicPlaceholder,
    },
    {
      title: '/',
      channelData: data.moviesPlaceholder,
    },
    {
      title: '',
      channelData: data.sportPlaceholder,
    },
    {
      title: '',
      channelData: data.musicPlaceholder,
    },
    {
      title: ' ',
      channelData: data.kidsPlaceholder,
    },
    {
      title: '',
      channelData: data.documentaryPlaceholder,
    },
    {
      title: 'HD',
      channelData: data.addOnsPlaceholder,
    },

  ]

  return (
    <>
      <section id='channels' className='centering spacing-top'></section>
      <div className='row'>
        <SectionTitle>
          <h2>  <br />  </h2>
          <hr className='center' />
        </SectionTitle>

        <div className='container'>
          <TabularSection
            channels={channelInfo}
          />
        </div>
      </div>
    </>
  )
}

const TabularSection = ({ channels }) => {
  const [selectedTab, setTab] = useState(0);
  const [show, setShow] = useState(false);

  return (
    <div>
      <hr className='normal' />
      <div className='mt-3 d-inline-block row d-flex justify-content-between channels-tab'>
        {channels.map(({ title }, index) => (
          <button
            key={index}
            className={'clear channel-selection d-inline ' + (selectedTab === index && 'active')}
            onClick={(e) => { setTab(index) }}
          >
            <h3 className='d-inline'>{title}</h3>
          </button>
        ))}
      </div>
      <div className='my-4 text-center'>
        <ChannelRow
          channels={channels[selectedTab].channelData}
        />
        <button onClick={(_) => setShow(true)} className='btn btn-secondary-no-outline mt-5'>  </button>
      </div>

      <PopupChannels
        show={show}
        setShow={(v) => { setShow(v) }}
      />
    </div>
  )
}

export default ChannelsSection;