import React from 'react';
import SectionTitle from '../shared/section-titles';
import ImageTextSection from '../index/image-text';
import { graphql, useStaticQuery } from 'gatsby';

import constants from './../../constants/home';

const ImageSection = ({ }) => {
  const data = useStaticQuery(graphql`
    query HomePageImageTextQuery {
      forInternet: file(relativePath: {eq: "index/for-home.png"}) {
        childImageSharp {
          fluid (maxHeight: 700) {
            ...GatsbyImageSharpFluid
          }
        }
      }
      forTV: file(relativePath: {eq: "index/for-office.png"}) {
        childImageSharp {
          fluid (maxHeight: 700) {
            ...GatsbyImageSharpFluid
          }
        }
      }
    }
  `);
  return (
    <>
      <section id='home-image-text'>
        <SectionTitle>
          <h2 style={{ whiteSpace: 'pre-wrap' }}>   Netsurf</h2>
          <hr className='center ' />
        </SectionTitle>

        <ImageTextSection
          imgSrc={data.forInternet.childImageSharp.fluid}
          imgAlt={' '}
          sectionTitle={<h2> <br /><span className='text-purple'>   </span></h2>}
          undertext={constants.forInternet.text}
          list={constants.forInternet.list}
          order={true}
        />

        <ImageTextSection
          imgSrc={data.forTV.childImageSharp.fluid}
          imgAlt={',   '}
          sectionTitle={<h2><br /><span className='text-purple'>   </span><br />   </h2>}
          undertext={constants.forTV.text}
          list={constants.forTV.list}
          order={false}
        />
      </section>
    </>
  );
}

export default ImageSection;