import React from 'react';
import styled from 'styled-components';
import Slider from "react-slick";

import facebookLogo from '../../images/home/facebook-review.svg';
import googleLogo from '../../images/home/google-review.svg';


const SliderBubble = styled.div`
  position: absolute;
  top: 25%;
  left: -8%;
  background: #158FCE;
  border-top-left-radius: 90%;
  border-top-right-radius: 60%;
  border-bottom-left-radius: 60%;
  border-bottom-right-radius: 120%;
  width: 140px;
  height: 140px;
  display: flex;
  justify-content: center;
  align-items:center;
  z-index: 1;
`;

const SliderBox = styled.div`
  position: relative;
  width: 700px;
  padding: 2rem 5rem 1rem 5rem;
  background: #FFFFFF;
  box-shadow: 0px 0px 28.5883px rgba(17, 6, 29, 0.2);
  border-radius: 6.01859px;
  transform: scale(0.9);
`;

const ReviewsSection = () => {
  let cards = [];
  for (var i = 0; i < 10; i++) {
    cards.push(
      <div key={i} className='mx-5 my-1'>
        <SliderBox className='review-card'>
          <SliderBubble>
            <h2 className='review text-white'>..</h2>
          </SliderBubble>
          <h3>O,     !</h3>
          <div className='pl-5'>
            <p>-  ,        ,     99.9% !</p>
            <p><strong> </strong> | <span className='text-grey'></span></p>
          </div>
        </SliderBox>
      </div>
    );
  }
  return (
    <>
      <section id='reviews-home-packages' className='centering spacing-top spacing-bottom'>
        <div className='row mx-5'>
          <div className='col-7' >
            <Slider {...settings}>
              {cards}
            </Slider>
          </div>
          <div className='col-5 d-flex justify-content-center align-items-center'>
            <div className='col-9'>
              <h2 className='text-left'>         </h2>
              <hr className='left' />
              <p>       ,        - .    .</p>

              <div className='row'>
                <div className='col-6 py-3 d-flex justify-content-center align-items-center reviews-border right'>
                  <div className='text-center'>
                    <h2 className='review'><span className='text-blue'>4.9</span></h2>
                    <Stars count={5} />
                    <img src={facebookLogo} className='img-fluid mt-2' alt='  Facebook' />
                  </div>
                </div>
                <div className='col-6 py-3 d-flex justify-content-center align-items-center reviews-border'>
                  <div className='text-center'>
                    <h2 className='review'><span className='text-blue'>4.9</span></h2>
                    <Stars count={5} />
                    <img src={googleLogo} className='img-fluid mt-2' alt='  Google' />
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </section>
    </>
  );
}

const Stars = ({ count }) => {
  let starList = [];
  for (var i = 0; i < count; i++) {
    starList.push(<span key={i} className='icon package star '></span>);
  }
  return (
    <div className='d-block'>
      {starList}
    </div>);
}

const settings = {
  autoplay: true,
  autoplaySpeed: 3500,
  dots: false,
  speed: 1000,
  arrows: false,
  infinite: true,
  vertical: true,
  slidesToShow: 3,
  slidesToScroll: 1,
  focusOnSelect: true,
  pauseOnFocus: true,

}

export default ReviewsSection;